body,
html {
  //background-color: $black;
  background: radial-gradient(50% 50% at 50% 50%, #262524 0%, #181715 52.08%, #040404 100%);
  color: #FFF;
  font-family: 'Roboto', sans-serif;
  padding: 0 !important;
  margin: 0;
  max-width: 100%;
  min-height: 100vh;
  overflow-x: hidden;
}

#root {
  min-height: 100vh !important;
}

a {
  text-decoration: none;
  outline: none;
  color: #FFF;
}

